//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name: 'FuturaFuturis') {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

//book
@font-face {
	@include fontdefault(normal, 400, 'FuturaBookC');
	@include fontpath('FuturaBookC Regular', 'FuturaBookC-Regular', FuturaBookC); }

//futuris
@font-face {
	@include fontdefault(normal, 300);
	@include fontpath('FuturaFuturis Light', 'FuturaFuturis-Light', FuturaFuturis-Light); }
@font-face {
	@include fontdefault(normal, 500);
	@include fontpath('FuturaFuturis Medium', 'FuturaFuturis-Medium', FuturaFuturis-Medium); }
@font-face {
	@include fontdefault(italic, 500);
	@include fontpath('FuturaFuturis MediumItalic', 'FuturaFuturis-MediumItalic', FuturaFuturis-MediumItalic); }
