@font-face {
  font-family: "FuturaBookC";
  font-style: normal;
  font-weight: 400;
  src: local("FuturaBookC Regular"), local("FuturaBookC-Regular"), url("../fonts/FuturaBookC.woff2") format("woff2"), url("../fonts/FuturaBookC.woff") format("woff");
}

@font-face {
  font-family: "FuturaFuturis";
  font-style: normal;
  font-weight: 300;
  src: local("FuturaFuturis Light"), local("FuturaFuturis-Light"), url("../fonts/FuturaFuturis-Light.woff2") format("woff2"), url("../fonts/FuturaFuturis-Light.woff") format("woff");
}

@font-face {
  font-family: "FuturaFuturis";
  font-style: normal;
  font-weight: 500;
  src: local("FuturaFuturis Medium"), local("FuturaFuturis-Medium"), url("../fonts/FuturaFuturis-Medium.woff2") format("woff2"), url("../fonts/FuturaFuturis-Medium.woff") format("woff");
}

@font-face {
  font-family: "FuturaFuturis";
  font-style: italic;
  font-weight: 500;
  src: local("FuturaFuturis MediumItalic"), local("FuturaFuturis-MediumItalic"), url("../fonts/FuturaFuturis-MediumItalic.woff2") format("woff2"), url("../fonts/FuturaFuturis-MediumItalic.woff") format("woff");
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  min-width: 320px;
  color: #0E0A1C;
  background: #fff;
  font-family: "FuturaBookC", Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
}

.out:after {
  content: " ";
  display: table;
  clear: both;
}

.main {
  overflow: auto;
  height: 100vh;
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 15px 20px;
  border-radius: 5px;
  color: #fff;
  background: #00CC75;
  font-family: "FuturaFuturis", Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
  transition: background 0.3s;
}

.no-touch .btn_green:hover {
  background: #19d182;
}

.no-touch .btn_green:active {
  background: #00b869;
}

.btn_green:active {
  background: #00b869;
}

.btn_wide {
  width: 100%;
}

.header {
  float: left;
  overflow: auto;
  width: 274px;
  height: 100vh;
  color: #fff;
  background: #0E0A1C;
  box-shadow: 0 22px 31px 0 rgba(0, 0, 0, 0.35);
  font-family: "FuturaBookC", Helvetica, sans-serif;
  font-weight: 400;
  text-align: center;
}

@media only screen and (max-width: 1023px) {
  .header {
    float: none;
    overflow: visible;
    width: auto;
    height: auto;
    min-height: 100px;
  }
}

@media only screen and (max-width: 767px) {
  .header {
    min-height: 50px;
    box-shadow: 0 11px 16px 0 rgba(0, 0, 0, 0.35);
  }
}

@media only screen and (max-width: 1023px) {
  .header__body {
    display: none;
  }
}

.header__order {
  padding: 40px 47px;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

.header__contacts {
  padding-bottom: 20px;
  font-size: 16px;
  line-height: 1;
}

.header__contacts li:not(:last-child) {
  margin-bottom: 10px;
}

.header__contacts a {
  display: block;
  color: inherit;
  transition: color 0.3s;
}

.no-touch .header__contacts a:hover {
  color: #66A6FF;
}

.header__address {
  padding: 0 30px 30px;
  font-size: 16px;
  line-height: 1.375;
}

.header__copyright {
  padding: 0 25px 30px;
  color: #9393A5;
  font-size: 14px;
  line-height: 1.286;
}

.btn-nav {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  width: 84px;
  height: 100px;
  background: none;
}

@media only screen and (max-width: 1023px) {
  .btn-nav {
    display: block;
  }
}

@media only screen and (max-width: 767px) {
  .btn-nav {
    width: 42px;
    height: 50px;
  }
}

.no-touch .btn-nav:hover:before {
  transform: translate3d(4px, 0, 0) rotate(45deg);
}

@media only screen and (max-width: 767px) {
  .no-touch .btn-nav:hover:before {
    transform: translate3d(2px, 0, 0) rotate(45deg);
  }
}

.no-touch .btn-nav:hover:after {
  transform: translate3d(4px, 0, 0) rotate(-45deg);
}

@media only screen and (max-width: 767px) {
  .no-touch .btn-nav:hover:after {
    transform: translate3d(2px, 0, 0) rotate(-45deg);
  }
}

.no-touch .btn-nav:hover .btn-nav__inner {
  opacity: 0;
  transform: scaleX(0);
}

.btn-nav:before, .btn-nav:after, .btn-nav__inner {
  position: absolute;
  left: 50%;
  margin-left: -12px;
  border-radius: 2.5px;
  width: 24px;
  height: 2px;
  background: #fff;
}

@media only screen and (max-width: 767px) {
  .btn-nav:before, .btn-nav:after, .btn-nav__inner {
    margin-left: -6px;
    border-radius: 1px;
    width: 12px;
    height: 1px;
  }
}

.btn-nav:before, .btn-nav:after {
  content: '';
  transition: transform 0.3s;
}

.btn-nav:before {
  top: 39px;
  transform-origin: left top;
}

@media only screen and (max-width: 767px) {
  .btn-nav:before {
    top: 19px;
  }
}

.btn-nav:after {
  top: 55px;
  transform-origin: left bottom;
}

@media only screen and (max-width: 767px) {
  .btn-nav:after {
    top: 27px;
  }
}

.btn-nav__inner {
  top: 47px;
  transition: opacity 0.3s, transform 0.3s;
}

@media only screen and (max-width: 767px) {
  .btn-nav__inner {
    top: 23px;
  }
}

.logo {
  display: block;
  padding: 31px 62px;
}

@media only screen and (max-width: 1023px) {
  .logo {
    padding: 26px 40px;
    margin: 0 auto;
    max-width: 200px;
  }
}

@media only screen and (max-width: 767px) {
  .logo {
    padding: 13px 20px;
    max-width: 100px;
  }
}

.logo img {
  display: block;
  width: 100%;
}

.lang {
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

@media only screen and (max-width: 1023px) {
  .lang {
    position: absolute;
    top: 0;
    right: 25px;
    border: none;
  }
}

@media only screen and (max-width: 767px) {
  .lang {
    right: 12px;
  }
}

.lang__item {
  display: inline-block;
  vertical-align: top;
  padding: 12px 5px;
  color: inherit;
  opacity: 0.44;
  font-size: 16px;
  line-height: 1;
  text-transform: uppercase;
  transition: opacity 0.3s;
}

@media only screen and (max-width: 1023px) {
  .lang__item {
    padding: 38px 5px 36px;
    font-size: 26px;
  }
}

@media only screen and (max-width: 767px) {
  .lang__item {
    padding: 19px 3px 18px;
    font-size: 13px;
  }
}

.lang__item.is-active {
  opacity: 1;
}

.nav {
  padding: 20px 0;
}

@media only screen and (max-width: 1023px) {
  .nav {
    padding: 14px 0 20px;
  }
}

.nav__link {
  position: relative;
  display: block;
  padding: 20px;
  color: #fff;
  opacity: 0.44;
  font-size: 16px;
  line-height: 1;
  transition: opacity 0.3s;
}

@media only screen and (max-width: 1023px) {
  .nav__link {
    padding: 30px;
    font-size: 30px;
  }
}

.no-touch .nav__link:hover {
  opacity: 1;
}

.nav__link.is-active {
  opacity: 1;
}

.nav__link.is-active span:before {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

.nav__link span {
  position: relative;
  display: inline-block;
  vertical-align: top;
}

.nav__link span:before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: -9px;
  border-top: 1px solid #00CC75;
  opacity: 0;
  transform: translate3d(0, 10px, 0);
  transition: opacity 0.3s, transform 0.3s;
}

.nav__footer {
  display: block;
  padding: 20px 40px;
  color: #66A6FF;
  font-size: 16px;
  line-height: 1.5;
}

@media only screen and (max-width: 1023px) {
  .nav__footer {
    padding: 30px 20px;
    margin: 0 auto;
    max-width: 380px;
    font-size: 30px;
    line-height: 1.467;
  }
}

.no-touch .nav__footer:hover span {
  border-color: #66A6FF;
}

.nav__footer span {
  border-bottom: 1px solid rgba(102, 166, 255, 0);
  transition: border-color 0.3s;
}

.cover {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.cover__content {
  padding: 25px 125px;
  margin-bottom: 110px;
  width: 100%;
  max-width: 700px;
  color: #fff;
  background: #000;
  font-family: "FuturaBookC", Helvetica, sans-serif;
  font-size: 25px;
  font-weight: 400;
  line-height: 1.2;
  text-align: center;
}

.error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 62px;
  height: 100%;
  text-align: center;
}

@media only screen and (max-width: 1023px) {
  .error {
    padding-top: 50px;
    padding-bottom: 50px;
    height: auto;
    min-height: 100%;
  }
}

.error__img {
  margin-bottom: 72px;
  width: 80%;
  max-width: 974px;
}

.error__description {
  padding: 0 20px;
  margin: 0 auto;
  max-width: 490px;
  font-family: "FuturaBookC", Helvetica, sans-serif;
  font-size: 25px;
  font-weight: 400;
  line-height: 1.2;
}

@media only screen and (max-width: 767px) {
  .error__description {
    max-width: 400px;
    font-size: 20px;
  }
}

@media only screen and (max-width: 599px) {
  .error__description {
    max-width: 310px;
    font-size: 15px;
  }
}
